import React, { useEffect, useState, useRef } from 'react';
import io from 'socket.io-client';
import axios from 'axios';
import UserProfile from '../UserProfile';
// import '../chat/ChatScreen.css';
import 'bootstrap/dist/css/bootstrap.min.css';

const socket = io("http://localhost:5000");

const ChatScreen = ({ receiverName, receiverId, receiverProfilePicture, isGroupChat }) => {
    const [messages, setMessages] = useState([]);
    const [newMessage, setNewMessage] = useState('');
    const [senderId, setSenderId] = useState(localStorage.getItem('userId'));
    const [isLoading, setIsLoading] = useState(false);
    const lastMessageRef = useRef(null);

    useEffect(() => {
        const userId = localStorage.getItem('userId');

        if (userId && receiverId) {
            setSenderId(userId);
            setIsLoading(true);

            if (isGroupChat) {
                socket.emit('joinRoom', { groupId: receiverId });
            } else {
                socket.emit('joinRoom', { userId });
                socket.emit('joinRoom', { userId: receiverId });
            }

            const fetchMessages = async () => {
                try {
                    let response;
                    if (isGroupChat) {
                        response = await axios.get(`http://localhost:5000/api/msg/group/${receiverId}`);
                    } else {
                        const response1 = await axios.get(`http://localhost:5000/api/msg/directMsgBetween/${senderId}/${receiverId}`);
                        const response2 = await axios.get(`http://localhost:5000/api/msg/directMsgBetween/${receiverId}/${senderId}`);
                        response = [...response1.data, ...response2.data];
                    }
                    setMessages(response);
                } catch (error) {
                    console.error('Error fetching messages:', error);
                } finally {
                    setIsLoading(false);
                }
            };

            fetchMessages();
        }

        socket.on('receiveMessage', (messageData) => {
            setMessages((prevMessages) => [...prevMessages, messageData]);
        });

        return () => {
            socket.off('receiveMessage');
        };
    }, [receiverId, isGroupChat]);

    useEffect(() => {
        if (lastMessageRef.current) {
            lastMessageRef.current.scrollIntoView({ behavior: 'smooth' });
        }
    }, [messages]);

    const handleSendMessage = async () => {
        if (newMessage.trim()) {
            const messageData = {
                content: newMessage,
                sender: senderId,
                receiver: isGroupChat ? null : receiverId,
                isDirectMsg: !isGroupChat,
                group: isGroupChat ? receiverId : null,
                type: 'msg',
                timestamp: Date.now()
            };

            try {
                const response = await axios.post(`http://localhost:5000/api/msg/`, messageData);
                if (response.status === 201) {
                    socket.emit('sendMessage', response.data);
                    setMessages((prevMessages) => [...prevMessages, response.data]);
                    setNewMessage('');
                }
            } catch (error) {
                console.error('Error saving the message:', error);
            }
        }
    };

    return (
        <div className="chat-container d-flex flex-column h-100">
            <div className="chat-header p-3 bg-light d-flex align-items-center border-bottom">
                {receiverProfilePicture && <img src={receiverProfilePicture} alt="receiver" className="rounded-circle me-2" width="40" height="40" />}
                <h5 className="mb-0">{receiverName}</h5>
            </div>

            <div className="messages-container flex-grow-1 p-3 overflow-auto">
                {messages.map((msg, index) => (
                    <div key={index} ref={index === messages.length - 1 ? lastMessageRef : null}
                        className={`d-flex my-2 ${msg.sender === senderId ? 'justify-content-end' : 'justify-content-start'}`}>
                        <div className={`p-2 rounded text-white ${msg.sender === senderId ? 'bg-success' : 'bg-secondary'}`}
                            style={{ maxWidth: '75%' }}>
                            <p className="mb-1">{msg.content}</p>
                            <small className="d-block text-end opacity-75">{new Date(msg.timestamp).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</small>
                        </div>
                    </div>
                ))}
            </div>

            <div className="input-group p-2 border-top bg-light">
                <input type="text" className="form-control" placeholder="Enter a message..." value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)} />
                <button className="btn btn-primary" onClick={handleSendMessage}>➤</button>
            </div>
        </div>
    );
};

export default ChatScreen;