import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
// import '../chat/ContactList.css';

const ContactList = ({ contacts, onContactClick, selectedContactId }) => {
  return (
    <div className="contacts-container list-group">
      {contacts.map((item) => {
        const isSelected = selectedContactId === (item.type === 'contact' ? item.receiver._id : item._id);

        return (
          <button
            key={item.type === 'contact' ? item.receiver._id : item._id}
            className={`list-group-item list-group-item-action d-flex align-items-center ${isSelected ? 'active' : ''}`}
            onClick={() => onContactClick(item)}
          >
            {item.type === 'contact' ? (
              <div className="d-flex align-items-center w-100">
                <div className="me-3">
                  {item.receiver.profilePicture ? (
                    <img src={item.receiver.profilePicture} alt={item.receiver.username} className="rounded-circle" width="40" height="40" />
                  ) : (
                    <div className="rounded-circle bg-secondary text-white d-flex justify-content-center align-items-center" style={{ width: '40px', height: '40px' }}>
                      {item.receiver.username.charAt(0).toUpperCase()}
                    </div>
                  )}
                </div>
                <div className="flex-grow-1">
                  <p className="mb-0 fw-bold">{item.receiver.username}</p>
                </div>
                <span className={`badge ${item.receiver.isOnline ? 'bg-success' : 'bg-secondary'}`}></span>
              </div>
            ) : (
              <div className="d-flex align-items-center w-100">
                <span className="me-3">👥</span>
                <p className="mb-0 fw-bold">{item.name}</p>
              </div>
            )}
          </button>
        );
      })}
    </div>
  );
};

export default ContactList;