import React, { useState, useEffect } from 'react';
import ContactList from './ContactList';
import SearchBar from './SearchBar';
import { io } from 'socket.io-client';
import 'bootstrap/dist/css/bootstrap.min.css';

const SideBar = ({ onContactClick }) => {
  const [contacts, setContacts] = useState([]);
  const [filteredContacts, setFilteredContacts] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [newContactUsername, setNewContactUsername] = useState('');
  const [isNewGroup, setIsNewGroup] = useState(false);
  const [newGroupCode, setNewGroupCode] = useState('');
  const [selectedContactId, setSelectedContactId] = useState(null);
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    fetchContacts();

    const socket = io(`http://localhost:5000`);
    const userId = localStorage.getItem('userId');

    if (userId) {
      socket.emit('joinRoom', { userId });
    }

    socket.on('new-contact', fetchContacts);
    socket.on('group-created', (newGroup) => {
      setContacts(prevContacts => [...prevContacts, { ...newGroup, type: 'group' }]);
      setFilteredContacts(prevFilteredContacts => [...prevFilteredContacts, { ...newGroup, type: 'group' }]);
    });

    return () => {
      socket.disconnect();
    };
  }, []);

  const fetchContacts = async () => {
    try {
      const token = localStorage.getItem('token');
      const userId = localStorage.getItem('userId');
      const response = await fetch(`http://localhost:5000/api/user/${userId}/contactsAndGroups`, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) throw new Error('Failed to fetch contacts and groups');
      const data = await response.json();

      const combinedData = [
        ...data.contacts.map(contact => ({ ...contact, type: 'contact' })),
        ...data.groups.map(group => ({ ...group, type: 'group' }))
      ];
      setContacts(combinedData);
      setFilteredContacts(combinedData);
    } catch (err) {
      console.error(err);
      setErrorMessage('An error occurred while fetching contacts');
    }
  };

  const handleSearch = (searchTerm) => {
    setSearchTerm(searchTerm);
    const filtered = contacts.filter((item) => {
      return item.type === 'contact'
        ? item.receiver.username.toLowerCase().includes(searchTerm.toLowerCase())
        : item.name.toLowerCase().includes(searchTerm.toLowerCase());
    });
    setFilteredContacts(filtered);
  };

  const toggleModal = () => {
    setShowModal(!showModal);
    setErrorMessage('');
  };

  const handleContactClick = (item) => {
    setSelectedContactId(item.type === 'contact' ? item.receiver._id : item._id);
    onContactClick(item);
  };

  return (
    <div className="sidebar container p-3">
      <SearchBar onSearch={handleSearch} />

      <div className="d-flex justify-content-between align-items-center mb-3">
        <h2 className="h5">My Contacts</h2>
        <button className="btn btn-primary btn-sm" onClick={toggleModal}>+</button>
      </div>

      {filteredContacts.length > 0 ? (
        <ContactList
          contacts={filteredContacts}
          onContactClick={handleContactClick}
          selectedContactId={selectedContactId}
        />
      ) : (
        <div className="alert alert-warning" role="alert">
          No contacts found
        </div>
      )}

      {showModal && (
        <div className="modal fade show d-block" tabIndex="-1">
          <div className="modal-dialog">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Add {isNewGroup ? 'Group' : 'Contact'}</h5>
                <button type="button" className="btn-close" onClick={toggleModal}></button>
              </div>
              <div className="modal-body">
                <div className="form-check form-switch mb-3">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    checked={isNewGroup}
                    onChange={() => {
                      setIsNewGroup(!isNewGroup);
                      setErrorMessage('');
                      setNewContactUsername('');
                      setNewGroupCode('');
                    }}
                  />
                  <label className="form-check-label">Toggle Group</label>
                </div>

                <input
                  className="form-control"
                  type="text"
                  placeholder={isNewGroup ? "Enter Group code" : "Enter username"}
                  value={isNewGroup ? newGroupCode : newContactUsername}
                  onChange={(e) => isNewGroup ? setNewGroupCode(e.target.value) : setNewContactUsername(e.target.value)}
                />
                {errorMessage && <div className="text-danger mt-2">{errorMessage}</div>}
              </div>
              <div className="modal-footer">
                <button className="btn btn-secondary" onClick={toggleModal}>Cancel</button>
                <button className="btn btn-primary" onClick={handleContactClick}>Add</button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default SideBar;
